.footerWrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgb(54, 58, 80);
  height: 50px;
  padding: 0 20px;
  z-index: 200;
}

.footerWrapper img {
  width: 25px;
  stroke-width: 2px;
}